/**
 * Integrao Firebase Sync com Notificaes Toast
 * Mostra notificaes quando sincronizao acontece
 */

import { firebaseSync } from './firebase-sync.js';
import { toast } from '../components/Toast.js';

class FirebaseSyncNotifications {
    constructor() {
        this.lastSyncedCount = 0;
        this.notificationDebounce = null;
        this.DEBOUNCE_DELAY = 2000; // 2 segundos para agrupar notificaes
        this.isSubscribed = false;
    }

    /**
     * Inicia as notificaes de sincronizao
     */
    start() {
        if (this.isSubscribed) return;

        // Inscrever-se nas mudanas de status do firebaseSync
        firebaseSync.subscribe((status) => {
            this._handleSyncStatus(status);
        });

        this.isSubscribed = true;
        console.log(' Notificaes de sincronizao ativadas');
    }

    /**
     * Para as notificaes de sincronizao
     */
    stop() {
        // As notificaes so automticas via subscribe, no h stop necessrio
        // mas podemos limpar debounce
        if (this.notificationDebounce) {
            clearTimeout(this.notificationDebounce);
            this.notificationDebounce = null;
        }
        this.isSubscribed = false;
    }

    /**
     * Manipula mudanas de status de sincronizao
     * @param {Object} status - Status atual da sincronizao
     */
    _handleSyncStatus(status) {
        // Mostrar notificao quando sincronizao completa
        if (status.synced && status.synced > 0) {
            this._notifySyncComplete(status.synced);
        }

        // Mostrar notificao quando ficar online e houver pendncias
        if (status.isOnline && status.hasPending && status.pendingCount > 0) {
            if (!status.syncing) {
                // Aguardar um pouco antes de notificar (pode estar sincronizando em breve)
                setTimeout(() => {
                    const currentStatus = {
                        isOnline: firebaseSync.getOnlineStatus(),
                        hasPending: firebaseSync.hasPendingOperations(),
                        pendingCount: firebaseSync.getPendingCount(),
                        syncing: false
                    };
                    
                    if (currentStatus.hasPending && !currentStatus.syncing) {
                        this._notifyPendingSync(currentStatus.pendingCount);
                    }
                }, 1000);
            }
        }

        // Mostrar notificao quando ficar offline
        if (!status.isOnline && status.hasPending) {
            this._notifyOffline(status.pendingCount);
        }
    }

    /**
     * Notifica sincronizao completa com debounce
     * @param {number} count - Nmero de itens sincronizados
     */
    _notifySyncComplete(count) {
        if (this.notificationDebounce) {
            clearTimeout(this.notificationDebounce);
        }

        this.notificationDebounce = setTimeout(() => {
            const message = count === 1 
                ? 'Dado sincronizado com sucesso!' 
                : `${count} dados sincronizados com sucesso!`;
            
            toast.success(message, {
                duration: 3000
            });

            this.lastSyncedCount = count;
        }, this.DEBOUNCE_DELAY);
    }

    /**
     * Notifica sobre operaes pendentes quando online
     * @param {number} count - Nmero de operaes pendentes
     */
    _notifyPendingSync(count) {
        const message = count === 1
            ? '1 operao pendente ser sincronizada...'
            : `${count} operaes pendentes sero sincronizadas...`;
        
        toast.info(message, {
            duration: 2000
        });
    }

    /**
     * Notifica quando ficou offline com operaes pendentes
     * @param {number} count - Nmero de operaes pendentes
     */
    _notifyOffline(count) {
        const message = count === 1
            ? '1 operao ser sincronizada quando voltar online'
            : `${count} operaes sero sincronizadas quando voltar online`;
        
        toast.info(message, {
            duration: 3000
        });
    }
}

// Exportar instncia singleton
export const firebaseSyncNotifications = new FirebaseSyncNotifications();
export default firebaseSyncNotifications;

