/**
 * Utilitrio para Verificar Variveis de Ambiente do Firebase
 * Verifica se todas as variveis necessrias esto configuradas
 */

/**
 * Verifica todas as variveis de ambiente do Firebase
 * @returns {Object} Resultado da verificao
 */
export function checkFirebaseEnv() {
    console.group(' Verificando Variveis de Ambiente Firebase');
    
    const requiredVars = [
        'VITE_FIREBASE_API_KEY',
        'VITE_FIREBASE_AUTH_DOMAIN',
        'VITE_FIREBASE_PROJECT_ID',
        'VITE_FIREBASE_STORAGE_BUCKET',
        'VITE_FIREBASE_MESSAGING_SENDER_ID',
        'VITE_FIREBASE_APP_ID'
    ];
    
    let allPresent = true;
    const results = {};
    
    requiredVars.forEach(varName => {
        const value = import.meta.env[varName];
        const isPresent = !!value && typeof value === 'string' && value.trim() !== '';
        
        if (!isPresent) {
            allPresent = false;
        }
        
        results[varName] = {
            present: isPresent,
            value: isPresent && value ? (value.length > 20 ? value.substring(0, 20) + '...' : value) : undefined,
            length: value && typeof value === 'string' ? value.length : 0
        };
        
        if (isPresent && value) {
            const displayValue = value.length > 20 ? value.substring(0, 20) + '...' : value;
            console.log(` ${varName}: ${displayValue} (${value.length} chars)`);
        } else {
            console.log(` ${varName}: NO ENCONTRADO ou VAZIO`);
        }
    });
    
    console.groupEnd();
    
    if (allPresent) {
        console.log(' Todas as variveis esto presentes e preenchidas!');
        console.log(' Se ainda v erro, verifique se os valores esto corretos.');
    } else {
        console.error(' Algumas variveis esto faltando ou vazias!');
        console.log(' Verifique:');
        console.log('1. Arquivo .env.local existe na raiz do projeto?');
        console.log('2. Nomes das variveis comeam com VITE_?');
        console.log('3. Todos os valores esto preenchidos (no vazios)?');
        console.log('4. Servidor foi reiniciado aps criar/editar .env.local?');
        console.log('\n Formato correto do .env.local:');
        console.log('   VITE_FIREBASE_API_KEY=valor');
        console.log('   VITE_FIREBASE_AUTH_DOMAIN=valor');
        console.log('   ... (sem espaos, sem aspas)');
    }
    
    return results;
}

// Executar automaticamente em desenvolvimento
if (import.meta.env.DEV) {
    // Aguardar um pouco para garantir que tudo carregou
    setTimeout(() => {
        checkFirebaseEnv();
    }, 1000);
}

// Exportar funo globalmente para uso no console do navegador
try {
    if (typeof window !== 'undefined') {
        window.checkFirebaseEnv = checkFirebaseEnv;
    }
} catch (error) {
    // Ignorar erros se window no estiver disponvel
}

