/**
 * Gerenciamento de Sincronizao Firebase
 * Gerencia fila de sincronizao offline/online e status de conexo
 */

import { firebaseService } from './firebase-service.js';
import { firebaseCache } from './firebase-cache.js';

const SYNC_QUEUE_KEY = 'firebase-sync-queue';
const SYNC_STATUS_KEY = 'firebase-sync-status';
const MAX_RETRIES = 3;
const RETRY_DELAY = 5000; // 5 segundos

class FirebaseSync {
    constructor() {
        this.queue = [];
        this.isOnline = navigator.onLine;
        this.syncInProgress = false;
        this.listeners = [];
        this.retryTimeout = null;
        
        // Inicializar
        this._init();
    }

    /**
     * Inicializao assncrona
     */
    async _init() {
        await firebaseCache.init();
        await this.loadQueue();
        this._setupOnlineOfflineListeners();
    }

    /**
     * Configura listeners para eventos online/offline
     */
    _setupOnlineOfflineListeners() {
        window.addEventListener('online', () => {
            console.log(' Conexo restaurada');
            this.handleOnline();
        });

        window.addEventListener('offline', () => {
            console.log(' Conexo perdida');
            this.handleOffline();
        });
    }

    /**
     * Retorna status online/offline
     * @returns {boolean}
     */
    getOnlineStatus() {
        return this.isOnline && firebaseService.isAvailable();
    }

    /**
     * Carrega fila de sincronizao do IndexedDB
     */
    async loadQueue() {
        try {
            const savedQueue = await firebaseCache.get(SYNC_QUEUE_KEY);
            if (savedQueue && Array.isArray(savedQueue)) {
                this.queue = savedQueue;
                console.log(` Fila carregada: ${this.queue.length} operaes pendentes`);
            }
        } catch (error) {
            console.error('Erro ao carregar fila de sincronizao:', error);
            this.queue = [];
        }
    }

    /**
     * Salva fila de sincronizao no IndexedDB
     */
    async saveQueue() {
        try {
            await firebaseCache.set(SYNC_QUEUE_KEY, this.queue);
        } catch (error) {
            console.error('Erro ao salvar fila de sincronizao:', error);
        }
    }

    /**
     * Adiciona operao  fila de sincronizao
     * @param {Object} operation - Operao a adicionar
     */
    async addToQueue(operation) {
        const queueItem = {
            id: this._generateId(),
            type: operation.type, // 'CREATE', 'UPDATE', 'DELETE'
            collection: operation.collection,
            docId: operation.docId,
            data: operation.data,
            timestamp: new Date().toISOString(),
            retries: 0,
            status: 'PENDING'
        };

        this.queue.push(queueItem);
        await this.saveQueue();
        
        // Tentar sincronizar imediatamente se online
        if (this.getOnlineStatus() && !this.syncInProgress) {
            this.sync().catch(error => {
                console.error('Erro ao sincronizar aps adicionar  fila:', error);
            });
        }

        this.notifyListeners();
    }

    /**
     * Processa fila de sincronizao
     */
    async sync() {
        if (!firebaseService.isAvailable() || !this.getOnlineStatus()) {
            return;
        }

        if (this.syncInProgress) {
            console.log(' Sincronizao j em progresso...');
            return;
        }

        if (this.queue.length === 0) {
            return;
        }

        this.syncInProgress = true;
        this.notifyListeners();

        console.log(` Iniciando sincronizao: ${this.queue.length} operaes`);

        const operationsToSync = [...this.queue];
        const successful = [];
        const failed = [];

        for (const item of operationsToSync) {
            try {
                await this._executeOperation(item);
                successful.push(item.id);
            } catch (error) {
                console.error(`Erro ao sincronizar operao ${item.id}:`, error);
                
                item.retries++;
                if (item.retries >= MAX_RETRIES) {
                    item.status = 'FAILED';
                    failed.push(item.id);
                } else {
                    item.status = 'RETRYING';
                }
            }
        }

        // Remover operaes bem-sucedidas da fila
        this.queue = this.queue.filter(item => !successful.includes(item.id));
        
        // Remover operaes que falharam aps mximo de tentativas
        const beforeFailedCount = this.queue.length;
        this.queue = this.queue.filter(item => item.status !== 'FAILED');
        const removedFailedCount = beforeFailedCount - this.queue.length;

        await this.saveQueue();
        this.syncInProgress = false;

        // Notificar resultado
        const syncedCount = successful.length;
        if (syncedCount > 0) {
            console.log(` Sincronizao concluda: ${syncedCount} operaes`);
            this.notifyListeners({ synced: syncedCount });
        }

        if (removedFailedCount > 0) {
            console.warn(` ${removedFailedCount} operaes falharam aps ${MAX_RETRIES} tentativas`);
        }

        // Se ainda h operaes pendentes, agendar retry
        if (this.queue.length > 0) {
            this._scheduleRetry();
        }
    }

    /**
     * Executa uma operao individual
     * @param {Object} item - Item da fila
     */
    async _executeOperation(item) {
        switch (item.type) {
            case 'CREATE':
            case 'UPDATE':
                await firebaseService.setDocument(
                    item.collection,
                    item.docId,
                    item.data,
                    item.type === 'UPDATE'
                );
                break;

            case 'DELETE':
                await firebaseService.deleteDocument(item.collection, item.docId);
                break;

            default:
                throw new Error(`Tipo de operao desconhecido: ${item.type}`);
        }
    }

    /**
     * Agenda retry para operaes pendentes
     */
    _scheduleRetry() {
        if (this.retryTimeout) {
            clearTimeout(this.retryTimeout);
        }

        this.retryTimeout = setTimeout(() => {
            if (this.getOnlineStatus() && this.queue.length > 0) {
                this.sync().catch(error => {
                    console.error('Erro no retry de sincronizao:', error);
                });
            }
        }, RETRY_DELAY);
    }

    /**
     * Manipula evento online
     */
    async handleOnline() {
        this.isOnline = true;
        this.notifyListeners();

        // Tentar sincronizar se h operaes pendentes
        if (this.queue.length > 0 && !this.syncInProgress) {
            console.log(' Tentando sincronizar operaes pendentes...');
            await this.sync();
        }
    }

    /**
     * Manipula evento offline
     */
    handleOffline() {
        this.isOnline = false;
        this.notifyListeners();

        if (this.retryTimeout) {
            clearTimeout(this.retryTimeout);
            this.retryTimeout = null;
        }
    }

    /**
     * Verifica se h operaes pendentes
     * @returns {boolean}
     */
    hasPendingOperations() {
        return this.queue.length > 0;
    }

    /**
     * Retorna nmero de operaes pendentes
     * @returns {number}
     */
    getPendingCount() {
        return this.queue.length;
    }

    /**
     * Inscreve-se em mudanas de status
     * @param {Function} callback - Funo callback(status)
     * @returns {Function} Funo para cancelar inscrio
     */
    subscribe(callback) {
        this.listeners.push(callback);
        
        // Notificar imediatamente com status atual
        callback(this._getStatus());

        return () => {
            this.listeners = this.listeners.filter(l => l !== callback);
        };
    }

    /**
     * Retorna status atual de sincronizao
     * @returns {Object}
     */
    _getStatus() {
        return {
            isOnline: this.getOnlineStatus(),
            hasPending: this.hasPendingOperations(),
            pendingCount: this.getPendingCount(),
            syncing: this.syncInProgress
        };
    }

    /**
     * Notifica todos os listeners sobre mudanas
     * @param {Object} extraData - Dados extras para incluir na notificao
     */
    notifyListeners(extraData = {}) {
        const status = {
            ...this._getStatus(),
            ...extraData
        };

        this.listeners.forEach(callback => {
            try {
                callback(status);
            } catch (error) {
                console.error('Erro ao notificar listener:', error);
            }
        });
    }

    /**
     * Limpa a fila de sincronizao
     */
    async clearQueue() {
        this.queue = [];
        await this.saveQueue();
        this.notifyListeners();
    }

    /**
     * Gera ID nico para itens da fila
     * @returns {string}
     */
    _generateId() {
        return Date.now().toString(36) + Math.random().toString(36).substr(2);
    }
}

// Exportar instncia singleton
export const firebaseSync = new FirebaseSync();
export default firebaseSync;
