/**
 * Servio Firebase - Implementao Real
 * Gerencia operaes CRUD e listeners real-time com Firestore
 */

import { db } from '../config/firebase.js';
import {
    doc,
    getDoc,
    setDoc,
    updateDoc,
    deleteDoc,
    collection,
    query,
    getDocs,
    where,
    orderBy,
    limit as firestoreLimit,
    writeBatch,
    onSnapshot,
    serverTimestamp,
    Timestamp
} from 'firebase/firestore';

const USER_ID = 'default'; // Single-user MVP

class FirebaseService {
    constructor() {
        this.db = db;
        this.listeners = new Map();
        this.available = false;
        
        // Verificar disponibilidade
        if (this.db) {
            this.available = true;
            console.log(' Firebase Service disponvel');
        } else {
            console.warn(' Firebase Service no disponvel - modo offline apenas');
        }
    }

    /**
     * Verifica se Firebase est disponvel
     * @returns {boolean}
     */
    isAvailable() {
        return this.available && this.db !== null;
    }

    /**
     * Obtm caminho da coleo para o usurio
     * @param {string} collectionName - Nome da coleo
     * @returns {CollectionReference}
     */
    _getCollectionPath(collectionName) {
        if (!this.isAvailable()) return null;
        return collection(this.db, 'users', USER_ID, collectionName);
    }

    /**
     * Obtm referncia do documento
     * @param {string} collectionName - Nome da coleo
     * @param {string} docId - ID do documento
     * @returns {DocumentReference}
     */
    _getDocRef(collectionName, docId) {
        if (!this.isAvailable()) return null;
        return doc(this.db, 'users', USER_ID, collectionName, docId);
    }

    /**
     * Busca um documento
     * @param {string} collectionName - Nome da coleo
     * @param {string} docId - ID do documento
     * @returns {Promise<Object|null>}
     */
    async getDocument(collectionName, docId) {
        if (!this.isAvailable()) return null;

        try {
            const docRef = this._getDocRef(collectionName, docId);
            const docSnap = await getDoc(docRef);

            if (docSnap.exists()) {
                return {
                    id: docSnap.id,
                    ...docSnap.data()
                };
            }
            return null;
        } catch (error) {
            console.error(`Erro ao buscar documento ${collectionName}/${docId}:`, error);
            throw error;
        }
    }

    /**
     * Busca uma coleo com filtros e ordenao
     * @param {string} collectionName - Nome da coleo
     * @param {Array} filters - Array de filtros [{field, operator, value}]
     * @param {string} orderByField - Campo para ordenao
     * @param {string} orderDirection - Direo da ordenao ('asc' | 'desc')
     * @param {number} limit - Limite de resultados (opcional)
     * @returns {Promise<Array>}
     */
    async getCollection(collectionName, filters = [], orderByField = null, orderDirection = 'asc', limit = null) {
        if (!this.isAvailable()) return [];

        try {
            const collectionRef = this._getCollectionPath(collectionName);
            if (!collectionRef) return [];

            let q = query(collectionRef);

            // Aplicar filtros
            filters.forEach(filter => {
                q = query(q, where(filter.field, filter.operator, filter.value));
            });

            // Aplicar ordenao
            if (orderByField) {
                q = query(q, orderBy(orderByField, orderDirection));
            }

            // Aplicar limite
            if (limit) {
                q = query(q, firestoreLimit(limit));
            }

            const querySnapshot = await getDocs(q);
            const results = [];

            querySnapshot.forEach((docSnap) => {
                results.push({
                    id: docSnap.id,
                    ...docSnap.data()
                });
            });

            return results;
        } catch (error) {
            console.error(`Erro ao buscar coleo ${collectionName}:`, error);
            throw error;
        }
    }

    /**
     * Cria ou atualiza um documento
     * @param {string} collectionName - Nome da coleo
     * @param {string} docId - ID do documento
     * @param {Object} data - Dados do documento
     * @param {boolean} merge - Se true, faz merge; se false, sobrescreve
     * @returns {Promise<string>} ID do documento
     */
    async setDocument(collectionName, docId, data, merge = true) {
        if (!this.isAvailable()) return docId || null;

        try {
            const docRef = this._getDocRef(collectionName, docId || this._generateId());
            
            const docData = {
                ...data,
                _lastModified: serverTimestamp(),
                _userId: USER_ID
            };

            await setDoc(docRef, docData, { merge });
            return docRef.id;
        } catch (error) {
            console.error(`Erro ao salvar documento ${collectionName}/${docId}:`, error);
            throw error;
        }
    }

    /**
     * Atualiza campos especficos de um documento
     * @param {string} collectionName - Nome da coleo
     * @param {string} docId - ID do documento
     * @param {Object} updates - Campos a atualizar
     * @returns {Promise<string>} ID do documento
     */
    async updateDocument(collectionName, docId, updates) {
        if (!this.isAvailable()) return docId || null;

        try {
            const docRef = this._getDocRef(collectionName, docId);
            
            const updateData = {
                ...updates,
                _lastModified: serverTimestamp()
            };

            await updateDoc(docRef, updateData);
            return docId;
        } catch (error) {
            console.error(`Erro ao atualizar documento ${collectionName}/${docId}:`, error);
            throw error;
        }
    }

    /**
     * Deleta um documento
     * @param {string} collectionName - Nome da coleo
     * @param {string} docId - ID do documento
     * @returns {Promise<void>}
     */
    async deleteDocument(collectionName, docId) {
        if (!this.isAvailable()) return;

        try {
            const docRef = this._getDocRef(collectionName, docId);
            await deleteDoc(docRef);
        } catch (error) {
            console.error(`Erro ao deletar documento ${collectionName}/${docId}:`, error);
            throw error;
        }
    }

    /**
     * Executa mltiplas operaes em lote
     * @param {Array} operations - Array de operaes [{type, collection, docId, data}]
     * @returns {Promise<void>}
     */
    async batchWrite(operations) {
        if (!this.isAvailable() || !operations || operations.length === 0) return;

        try {
            const batch = writeBatch(this.db);
            const BATCH_LIMIT = 500; // Limite do Firestore

            for (let i = 0; i < operations.length; i += BATCH_LIMIT) {
                const batchOps = operations.slice(i, i + BATCH_LIMIT);
                const currentBatch = writeBatch(this.db);

                batchOps.forEach(op => {
                    const docRef = this._getDocRef(op.collection, op.docId || this._generateId());
                    const docData = {
                        ...op.data,
                        _lastModified: serverTimestamp(),
                        _userId: USER_ID
                    };

                    switch (op.type) {
                        case 'SET':
                            currentBatch.set(docRef, docData, { merge: op.merge !== false });
                            break;
                        case 'UPDATE':
                            currentBatch.update(docRef, docData);
                            break;
                        case 'DELETE':
                            currentBatch.delete(docRef);
                            break;
                        default:
                            console.warn(`Tipo de operao desconhecido: ${op.type}`);
                    }
                });

                await currentBatch.commit();
            }
        } catch (error) {
            console.error('Erro ao executar batch write:', error);
            throw error;
        }
    }

    /**
     * Inscreve-se em mudanas de um documento (real-time)
     * @param {string} collectionName - Nome da coleo
     * @param {string} docId - ID do documento
     * @param {Function} callback - Funo callback(doc) chamada quando h mudanas
     * @returns {Function} Funo para cancelar a inscrio
     */
    subscribeToDocument(collectionName, docId, callback) {
        if (!this.isAvailable()) {
            return () => {}; // Retorna funo vazia se no disponvel
        }

        try {
            const docRef = this._getDocRef(collectionName, docId);
            const listenerId = `${collectionName}/${docId}`;

            const unsubscribe = onSnapshot(docRef, (docSnap) => {
                if (docSnap.exists()) {
                    callback({
                        id: docSnap.id,
                        ...docSnap.data()
                    });
                } else {
                    callback(null);
                }
            }, (error) => {
                console.error(`Erro no listener de documento ${collectionName}/${docId}:`, error);
            });

            // Armazenar unsubscribe function
            this.listeners.set(listenerId, unsubscribe);

            // Retornar funo para cancelar
            return () => {
                unsubscribe();
                this.listeners.delete(listenerId);
            };
        } catch (error) {
            console.error(`Erro ao inscrever-se em documento ${collectionName}/${docId}:`, error);
            return () => {};
        }
    }

    /**
     * Inscreve-se em mudanas de uma coleo (real-time)
     * @param {string} collectionName - Nome da coleo
     * @param {Function} callback - Funo callback(docs) chamada quando h mudanas
     * @param {Array} filters - Array de filtros opcionais
     * @returns {Function} Funo para cancelar a inscrio
     */
    subscribeToCollection(collectionName, callback, filters = []) {
        if (!this.isAvailable()) {
            return () => {}; // Retorna funo vazia se no disponvel
        }

        try {
            const collectionRef = this._getCollectionPath(collectionName);
            if (!collectionRef) return () => {};

            let q = query(collectionRef);

            // Aplicar filtros
            filters.forEach(filter => {
                q = query(q, where(filter.field, filter.operator, filter.value));
            });

            const listenerId = `collection/${collectionName}`;

            const unsubscribe = onSnapshot(q, (querySnapshot) => {
                const docs = [];
                querySnapshot.forEach((docSnap) => {
                    docs.push({
                        id: docSnap.id,
                        ...docSnap.data()
                    });
                });
                callback(docs);
            }, (error) => {
                console.error(`Erro no listener de coleo ${collectionName}:`, error);
            });

            // Armazenar unsubscribe function
            this.listeners.set(listenerId, unsubscribe);

            // Retornar funo para cancelar
            return () => {
                unsubscribe();
                this.listeners.delete(listenerId);
            };
        } catch (error) {
            console.error(`Erro ao inscrever-se em coleo ${collectionName}:`, error);
            return () => {};
        }
    }

    /**
     * Cancela todas as inscries ativas
     */
    unsubscribeAll() {
        this.listeners.forEach((unsubscribe) => {
            try {
                unsubscribe();
            } catch (error) {
                console.error('Erro ao cancelar listener:', error);
            }
        });
        this.listeners.clear();
    }

    /**
     * Gera um ID nico para documentos
     * Usa a funo do Firestore para gerar ID nico
     * @returns {string}
     */
    _generateId() {
        if (!this.isAvailable()) {
            // Fallback: gerar ID simples se Firebase no disponvel
            return Date.now().toString(36) + Math.random().toString(36).substr(2);
        }
        // Usar collection temporria apenas para gerar ID
        const tempRef = collection(this.db, '_temp');
        return doc(tempRef).id;
    }
}

// Exportar instncia singleton
export const firebaseService = new FirebaseService();
export default firebaseService;
